.container {
  width: 100%;
  color: white;
  padding: 0 9.5%;
  background-color: #09090a;
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 10%;
  padding-right: 10%;
}

.containerHome{
  width: 100%;
  color: white;
  background-color: #09090a;
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 10%;
  padding-right: 10%;
  position: fixed;
  z-index: 999;
}

.image {
  margin-top: 15px;
  width: 150px;
  cursor: pointer;
}

.nav {
  position: relative;
  width: 50%;
  height: auto;
  display: flex;
  justify-content: space-evenly;
  cursor: pointer;
}

.nav .link {
  position: relative;
  line-height: 22px;
  font-style: normal;
  font-variant: small-caps;
  color: #ffffff;
  text-decoration: none;
  list-style: none;
  font-weight: 500;
  font-size: 1rem;
  transition: 0.5s;
}
.nav .link:before,
.nav .link:after {
  content: "";
  position: absolute;
  height: 3px;
  width: 0;
  background-color: #00b37e;
  transition: 0.5s;
}
.nav .link:after {
  left: 0;
  bottom: -5px;
  transition: 0.5s;
}
.nav .link:before{
  right: 0;
  top: -5px;
}
.nav .link:hover {
  color: #ffffff;
}
.nav .link:hover:after,
.nav .link:hover:before{
  width: 100%;
}
.btnLogin {
  width: 140px;
  height: 40px;
  color: #ffffff;
  background-color: #09090a;
  padding: 10px 40px;
  border: 2px solid #00b37e;
  border-radius: 4px;
  font-size: 1.125rem;
  padding-top: 8px;
  font-weight: 700;
  font-variant: small-caps;
  transition: 0.3s;
}
.btnLogin:hover, .btnCadastro:hover {
  background-color: #00b37e;
  color: black;
  cursor: pointer;
}

.btnCadastro {
  width: 140px;
  height: 40px;
  color: #ffffff;
  background-color: #09090a;
  padding: 10px;
  border: 2px solid #00b37e;
  border-radius: 4px;
  font-size: 1.1rem;
  padding-top: 8px;
  font-weight: 700;
  font-variant: small-caps;
  transition: 0.3s;
}
